
.publishers__container {
  background: url(/static/images/editoras/background.jpg);
  box-sizing: border-box;
  position: relative;
  background-size: 100%;
  background-repeat: no-repeat;
}
.publishers__container a {
    color: #fff;
}
.publishers__container .title {
    text-align: center;
    color: #fff;
    font-weight: bold;
    padding: 50px 50px 0;
    margin: 0;
    text-transform: uppercase;
}
.publishers__container .title span {
      color: #EC3680;
      font-weight: bold;
}
.publishers__container .content {
    text-align: left;
    width: 45%;
    margin: 0 auto 0 12.5%;
    color: #fff;
    line-height: 1.6;
}
@media (max-width: 600px) {
.publishers__container .content {
        width: 75%;
        margin: 0 auto;
}
}
.publishers__container .section-image {
    position: absolute;
    top: -602px;
    right: 67px;
}
@media (max-width: 600px) {
.publishers__container .section-image {
        display: none;
}
}
.publishers__container .publishers {
    width: 75%;
    margin: 110px auto 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
@media (max-width: 600px) {
.publishers__container .publishers {
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-direction: column;
                flex-direction: column;
}
}
.publishers__container .publishers__item {
      box-sizing: border-box;
      width: 49%;
      padding: 45px;
      border-radius: 15px;
      background-color: #3493D6;
      margin-bottom: 80px;
}
@media (max-width: 600px) {
.publishers__container .publishers__item {
          width: 100%;
          box-sizing: border-box;
          padding: 45px 25px;
          margin-bottom: 80px;
}
.publishers__container .publishers__item.blank {
            display: none;
}
}
.publishers__container .publishers__item .system-brand {
        display: block;
        margin: -100px auto 25px;
        width: 110px;
        background: white;
        border-radius: 19px;
        height: 115px;
        box-sizing: border-box;
        border: 6px solid white;
        overflow: hidden;
}
.publishers__container .publishers__item .system-brand img {
          width: 110px;
          margin: -6px;
}
.publishers__container .publishers__item .system-brand.no-border {
          border: none;
}
.publishers__container .publishers__item .system-brand.no-border img {
            margin: 0;
}
.publishers__container .publishers__item h2 {
        font-size: 18px;
        font-weight: bold;
        color: #fff;
}
.publishers__container .publishers__item p {
        font-size: 14px;
        font-weight: normal;
        color: #fff;
}
.publishers__container .publishers__item.blank {
        background-color: transparent;
}
.publishers__container .publishers__item.bg-green {
        background-color: #2EC771;
}
.publishers__container .publishers__item.bg-red {
        background-color: #E01316;
}



