.shake {
  -webkit-animation: shake 0.5s cubic-bezier(0.25, 0.8, 0.25, 1);
          animation: shake 0.5s cubic-bezier(0.25, 0.8, 0.25, 1);
  background: #ff0;
}
.modal-close {
  padding: 0;
  background: transparent;
  border: none;
}
.icon-clear {
  background: url(/static/images/icons/icon-clear.svg);
  width: 24px;
  height: 24px;
  display: inline-block;
}
.icon-menu {
  background: url(/static/images/icons/icon-menu.svg);
  width: 24px;
  height: 24px;
  display: inline-block;
}
div:focus {
  outline: 0;
}
@-webkit-keyframes shake {
8%, 41% {
    -webkit-transform: translateX(em(-10, 16));
            transform: translateX(em(-10, 16));
}
25%, 58% {
    -webkit-transform: translateX(em(10, 16));
            transform: translateX(em(10, 16));
}
75% {
    -webkit-transform: translateX(em(-5, 16));
            transform: translateX(em(-5, 16));
}
92% {
    -webkit-transform: translateX(em(5, 16));
            transform: translateX(em(5, 16));
}
0%, 100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
}
@keyframes shake {
8%, 41% {
    -webkit-transform: translateX(em(-10, 16));
            transform: translateX(em(-10, 16));
}
25%, 58% {
    -webkit-transform: translateX(em(10, 16));
            transform: translateX(em(10, 16));
}
75% {
