
.md-dialog-container {
  background-color: rgba(0, 0, 0, 0.7) !important;
}
.md-dialog {
  position: absolute;
  z-index: 2;
}
.veja-mais {
  text-decoration: underline;
  cursor: pointer;
}
.teaching__container {
  background-color: #e1e1e1;
  box-sizing: border-box;
}
.teaching__container a {
    color: #fff;
}
.teaching__container .title {
    text-align: center;
    color: #000;
    font-weight: bold;
    padding: 50px 50px 0;
    margin: 0;
    text-transform: uppercase;
}
.teaching__container .title span {
      color: #EC3680;
      font-weight: bold;
}
.teaching__container .content {
    text-align: justify;
    width: 75%;
    color: #000;
    line-height: 1.6;
    margin: 0 auto;
}
.teaching__container .teaching-systems {
    width: 75%;
    margin: 110px auto 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
@media (max-width: 600px) {
.teaching__container .teaching-systems {
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-direction: column;
                flex-direction: column;
}
}
.teaching__container .teaching-systems__item {
      box-sizing: border-box;
      width: 49%;
      padding: 45px;
      border-radius: 15px;
      background-color: #3493D6;
      margin-bottom: 80px;
}
@media (max-width: 600px) {
.teaching__container .teaching-systems__item {
          width: 100%;
          box-sizing: border-box;
          padding: 45px 25px;
          margin-bottom: 80px;
}
.teaching__container .teaching-systems__item.blank {
            display: none;
}
}
.teaching__container .teaching-systems__item .system-brand {
        width: 110px;
        display: block;
        margin: -100px auto 25px;
}
.teaching__container .teaching-systems__item h2 {
        font-size: 18px;
        font-weight: bold;
        color: #fff;
}
.teaching__container .teaching-systems__item p {
        font-size: 14px;
        font-weight: normal;
        color: #fff;
}
.teaching__container .teaching-systems__item.blank {
        background-color: transparent;
}
.teaching__container .teaching-systems__item.bg-green {
        background-color: #2EC771;
}
.teaching__container .teaching-systems__item.bg-red {
        background-color: #E01316;
}



