
.network__container {
  background-color: #fff;
  padding-bottom: 80px;
}
.network__container .title {
    text-align: center;
    color: #000;
    font-weight: bold;
    padding: 50px 50px 30px;
    margin: 0;
    text-transform: uppercase;
}
.network__container .title span {
      color: #EC3680;
      font-weight: bold;
}
.network__container .carousel-container {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    width: 84%;
    margin: 0 auto;
}
@media (max-width: 600px) {
.network__container .carousel-container {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
      -webkit-box-pack: justify;
          -ms-flex-pack: justify;
              justify-content: space-between;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      width: auto;
      margin: 0 auto;
}
}
.network__container .carousel-item {
    width: 300px;
    border-radius: 5px;
    border: 1px solid #000;
    background: no-repeat center top #fff;
    padding: 90px 20px 10px;
    box-sizing: border-box;
    background-position-y: -3px;
}
.network__container .carousel-item .avatar-container {
      border: 1px solid red;
      border-radius: 50px;
      width: 90px;
      height: 90px;
      margin: 0 auto;
}
.network__container .carousel-item .avatar-container img {
        border: 2px solid white;
        border-radius: 50px;
}
@media (max-width: 600px) {
.network__container .carousel-item {
      margin: 10px auto;
}
}



