
.md-dialog-container {
  background-color: rgba(0, 0, 0, 0.6) !important;
}
.md-dialog {
  position: absolute;
  z-index: 2;
}
.scroll {
  overflow: scroll !important;
}
.md-dialog-content img {
  max-width: 100%;
}
.veja-mais {
  text-decoration: underline;
  cursor: pointer;
  font-weight: normal;
}
.solutions__container {
  height: 630px;
  background: url(/static/images/solucao/background.jpg) no-repeat top center;
  background-size: 100%;
}
@media (max-width: 600px) {
.solutions__container {
      height: auto;
      background-size: auto 100%;
      padding-bottom: 49px;
}
}
.solutions__container a {
    color: #fff;
}
.solutions__container .title {
    text-align: center;
    color: #fff;
    font-weight: bold;
    padding: 50px 50px 0;
    margin: 0;
    text-transform: uppercase;
}
.solutions__container .content {
    text-align: justify;
    width: 75%;
    color: #fff;
    line-height: 1.6;
    margin: 0 auto;
}
.solutions__container .subtitle {
    font-size: 18px;
    font-weight: bold;
    padding: 45px 50px 30px;
    margin: 0 auto;
    color: #fff;
    text-align: center;
}
.solutions__container .programs__list {
    width: 75%;
    margin: 0 auto;
}
.solutions__container .programs-detail {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}
@media (max-width: 600px) {
.solutions__container .programs-detail {
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-direction: column;
                flex-direction: column;
}
}
.solutions__container .programs-detail__item {
      margin: 0 0 40px;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
      color: #fff;
      font-size: 14px;
      height: 120px;
      padding-left: 140px;
      background: no-repeat center left;
      width: 33%;
      font-weight: 500;
}
@media (max-width: 600px) {
.solutions__container .programs-detail__item {
          width: auto;
          background-size: 20%;
          padding-left: 80px;
          height: 52px;
          margin-bottom: 15px;
}
}
.solutions__container .programs-detail__item a {
        font-size: 12px;
        font-weight: 300;
}
.solutions__container .programs-detail__item.icon-first-line-1 {
        background-image: url(/static/images/solucao/icon-first-line-1.svg);
}
.solutions__container .programs-detail__item.icon-first-line-2 {
        background-image: url(/static/images/solucao/icon-first-line-2.svg);
}
.solutions__container .programs-detail__item.icon-first-line-3 {
        background-image: url(/static/images/solucao/icon-first-line-3.svg);
}
.solutions__container .programs-detail__item.icon-second-line-1 {
        background-image: url(/static/images/solucao/icon-second-line-1.svg);
}
.solutions__container .programs-detail__item.icon-second-line-2 {
        background-image: url(/static/images/solucao/icon-second-line-2.svg);
}
.solutions__container .programs-detail__item.icon-second-line-3 {
        background-image: url(/static/images/solucao/icon-second-line-3.svg);
}



