
.md-dialog-title {
  margin-bottom: 10px;
  padding: 12px 24px 0;
  text-align: right;
}
.results__container h1, .results__container h2 {
  text-align: center;
  color: #fff;
  font-size: 28px;
  text-transform: uppercase;
  margin: 0 0 30px 0;
}
.results__container .title {
  background-color: #C5C5C5;
  color: #000;
  font-weight: 700;
  padding: 40px 0;
  margin: 0;
}
.results__container .title span {
    color: #EC3680;
    font-weight: bold;
}
.results__container .aumento-indicadores {
  background-color: #2A80BE;
}
.results__container .desenvolvimento-pedagogico {
  background-color: #E12362;
}
.results__container .formacao-protagonistas {
  background-color: #DCAC14;
}
.results__container .results__list__item {
  padding: 20px 30px;
  color: #fff;
}
.results__container .results__list__item__title {
    position: relative;
    font-size: 20px;
}
.results__container .results__list__item__title .icon-exclamation {
      background: white;
      display: inline-block;
      margin-left: 5px;
      text-align: center;
      font-size: 14px;
      position: relative;
      cursor: pointer;
      top: -2px;
      border: none;
      font-weight: bold;
}
@media (max-width: 600px) {
.results__container .results__list__item__title .icon-exclamation {
          position: relative;
}
}
.results__container .results__list__item__title .icon-exclamation.blue {
        color: #2A80BE;
}
.results__container .results__list__item__title .icon-exclamation.pink {
        color: #E12362;
}
.results__container .results__list__item__title .icon-exclamation.yellow {
        color: #DCAC14;
}
.results__container .results-detail {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
@media (max-width: 600px) {
.results__container .results-detail {
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
}
}
.results__container .results-detail__item {
    margin: 0 30px 10px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    color: #fff;
    font-size: 16px;
    height: 78px;
    padding-left: 100px;
    background: no-repeat center left;
    width: 33%;
}
@media (max-width: 600px) {
.results__container .results-detail__item {
        box-sizing: border-box;
        margin: 0 0 10px 0;
        width: 100%;
}
}
.results__container .results-detail__item.icon-indicadores-1 {
      background-image: url(/static/images/resultados/icon-indicadores-1.svg);
}
.results__container .results-detail__item.icon-indicadores-2 {
      background-image: url(/static/images/resultados/icon-indicadores-2.svg);
}
.results__container .results-detail__item.icon-indicadores-3 {
      background-image: url(/static/images/resultados/icon-indicadores-3.svg);
}
.results__container .results-detail__item.icon-desenvolvimento-1 {
      background-image: url(/static/images/resultados/icon-desenvolvimento-1.svg);
}
.results__container .results-detail__item.icon-desenvolvimento-2 {
      background-image: url(/static/images/resultados/icon-desenvolvimento-2.svg);
}
.results__container .results-detail__item.icon-desenvolvimento-3 {
      background-image: url(/static/images/resultados/icon-desenvolvimento-3.svg);
}
.results__container .results-detail__item.icon-formacao-1 {
      background-image: url(/static/images/resultados/icon-formacao-1.svg);
}
.results__container .results-detail__item.icon-formacao-2 {
      background-image: url(/static/images/resultados/icon-formacao-2.svg);
}
.results__container .results-detail__item.icon-formacao-3 {
      background-image: url(/static/images/resultados/icon-formacao-3.svg);
}



