
.contact__container {
  background-color: #333;
}
.contact__container .title {
    text-align: center;
    color: #fff;
    font-weight: bold;
    padding: 50px 50px 0;
    margin: 0;
    text-transform: uppercase;
}
.contact__container .title span {
      color: #EC3680;
      font-weight: bold;
}
.contact__container .content {
    text-align: center;
    width: 75%;
    color: #fff;
    line-height: 1.6;
    margin: 0 auto;
    padding-bottom: 30px;
}
.contact__container .content span {
      font-size: 30px;
      font-weight: bold;
}
.contact__container .form-container {
    padding: 0 30px 70px;
    width: 700px;
    margin: 0 auto;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
@media (max-width: 600px) {
.contact__container .form-container {
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-direction: column;
                flex-direction: column;
        width: auto;
}
}
.contact__container .form-container__right, .contact__container .form-container__left {
      width: 50%;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
      -webkit-box-align: stretch;
          -ms-flex-align: stretch;
              align-items: stretch;
}
@media (max-width: 600px) {
.contact__container .form-container__right, .contact__container .form-container__left {
          width: 100%;
          box-sizing: border-box;
}
}
.contact__container .form-container__left {
      margin-right: 30px;
}
.contact__container .form-container .input-field {
      box-sizing: border-box;
}
.contact__container .form-container input, .contact__container .form-container textarea, .contact__container .form-container button {
      border: none;
      padding: 10px;
      margin: 5px;
      display: block;
      width: 100%;
      box-sizing: border-box;
      font-family: sans-serif;
      border-radius: 5px;
}
.contact__container .form-container button {
      color: #fff;
      background-color: #cc154a;
      cursor: pointer;
}
.contact__container .form-container button:hover {
        background-color: #ff5c8b;
}
.contact__container .form-container button:focus {
        background-color: #ff5c8b;
}
.contact__container .form-container #mensagem {
      -webkit-box-flex: 1;
          -ms-flex-positive: 1;
              flex-grow: 1;
}
.contact__container .footer {
    background-color: #000;
    color: #EC3680;
    text-align: center;
    padding: 20px;
    width: 100%;
    font-size: 14px;
    box-sizing: border-box;
}
.contact__container .footer a {
      color: #EC3680;
      text-decoration: none;
}
.contact__container .footer strong {
      color: #fff;
}



